/* ---------------------------------------------- */
/* --------- Page administrateur ---------------- */
/* ---------------------------------------------- */

import Cookies from 'js-cookie'
import { db } from '../config/firebase';
import React, { useState } from "react";
import Interfaceadmin from '../components/Interfaceadmin';
import { collection, query, where, getDocs } from "firebase/firestore";

export default function Admin() {

  // Variables dynamiques
  // variables d'erreur
	const [errorName, showErrorName] = useState(false)
  const [errorPass, showErrorPass] = useState(false)
  /* A chaque chargement de page, tout est recharg notamment ces variables, celle-ci 
  cherche notre cookie d'authentification, s'il n'existe pas alors cette variable est false */
  const [isConnected, giveAccess] = useState(Cookies.get("acsssn") || false) ;

  
  // supprime le cookie d'authentification et raffiche le menu de login
  function disconnectUser(){
    Cookies.remove('acsssn')
    giveAccess(false)
  }

  // fonction de traitement du formulaire
  async function handleSubmit (e) {
    // Empche le rafrachissement de page
    e.preventDefault();

    // import de la librairie de cryptage
    const bcrypt = require('bcryptjs');
    // console.log(bcrypt.hashSync("test", 10));
    // rcupration du nom et du mot de passe entrs dans le formulaire
    const formData = new FormData(e.target);
    const formProps = Object.fromEntries(formData);
    let { formName, formPassword } = formProps

    // Construction de la requte de rcupration du mot de passe associ au nom entr
    const queryFB = query(collection(db, "administrateurs"), where("nom", "==", formName));

    // xecution de la requte
    const queryResults = await getDocs(queryFB);
    
    // cas aucun administrateur avec ce nom n'a t rouv, donc inexistant
    if (queryResults.empty){
      showErrorName(true)
      showErrorPass(false)
    }
    
    // s'il y en a un, alors on vrifie le mot de passe
    else {
      // rcupration du hash du mdp de la bdd associ au nom
      let userDBHash = queryResults.docs[0].data().mdp

      /* le mot de passe entr est compar au hash du mdp enregistr dans la bdd
      (retourne true ou false), le mdp enregistr n'apparat jamais en clair */
      if (!bcrypt.compareSync(formPassword, userDBHash)){
        showErrorPass(true)
        showErrorName(false)
      }
      
      // si le mdp correspond, on autorise l'accs au compte administrateur  
      else {
        // on affiche la page admin, affich tant que le cookie est valide (et si page ouverte > 24h ???)
        giveAccess(true)  
        showErrorPass(false)
        showErrorName(false)
 
        // cration du cookie de session valide pendant 24h
        Cookies.set('acsssn', queryResults.docs[0].data().nom, { expires: 1, sameSite: false})
      }
    }
    
  };

  // rendu html
  return (
    <div className="interAdmin">
      {/* notre condition vrifie si un cookie d'authentification 
      est prsent, peu importe sa valeur. S'il y en a un alors on affiche la partie admin */}
      { isConnected ? (
        <>
          {/* bouton de dconnexion */}
          <button className='btn btn-danger mt-2 btn-sm float-right' onClick={() => disconnectUser()}>Se dconnecter</button>
          {/* composant d'interface d'administrateur */}
          <Interfaceadmin /> 
        </>
      // s'il n'y a pas de cookie, alors on laisse la partie login
      ) : (
        <>
          <div className="row text-center">
            <h2 className="text-center mt-3">Connexion administrateur</h2>

            {/* Formulaire de connexion admin */}
            <form onSubmit={handleSubmit} className="text-center mt-5 admin-co-div">
              <div className="ms-auto col col-sm-12 col-md-12 col-12 col-lg-12 ">
                {/* message d'erreur si nom pas reconnu */}
                <div className={!!(errorName) ? "msgError" : 'hide-error'}> Cet admin n'existe pas</div>
                <label className="col-2">Nom </label>
                {/* reactjs sanitize le rendu html pour viter les xss dans nos input */}
                <input type="text" className="col-2" name="formName" required />
              </div>

              <div className="mt-3">
                <div className={!!(errorPass) ? "msgError" : 'hide-error'}> Ce mot de passe est erron</div>
                <label className="col-2">Mot de passe </label>
                <input type="password" className="col-2" name="formPassword" required />
              </div>

              {/* bouton de soumission de notre formulaire */}
              <button type="submit" className="btn btn-danger mt-3">Se connecter</button>
            </form> 
          </div>     
        </>
      )}
    </div>
    
  );
}
